.header {
  height: 100vh;
  width: 100vw;
  background: {
    image: url('../../img/header-bg.svg');
    position: center center;
    repeat: no-repeat;
 }    // size: cover

  .header-text {
    text-align: center;
    color: $secondary;
    height: 80vh;
    @include flex-col;
    justify-content: center;
    align-items: center;

    .main-title {
      text-transform: uppercase;
      font-size: 2rem;
      line-height: 2.5rem; }

    .subtitle {
      font-size: 1rem; } } }

@media screen and (min-width: 600px) {
  .header {
    background: {
      image: url('../../img/header-large-bg.svg'); }

    .header-text {
      text-align: left;
      justify-content: flex-start;
      align-items: flex-start;
      margin-left: 150px;
      margin-top: 100px;

      .main-title {
        font-size: 3rem;
        line-height: 3.5rem; }

      .subtitle {
        font-size: 1.5rem; } } } }
