#about {
  @include flex-col;
  justify-content: center;
  align-items: center;
  text-align: center;
  @include section-mobile-margin;
  padding: 0 $gutterWidth;

  .profile-pic {
    width: 250px;
    height: 250px;
 }    // border-radius: 50%

  .personal-desc {
    @include line-height-1rem-text; }

  .development,
  .design {
    margin: $gutterWidth 0; }

  .grid {
    margin-top: $gutterWidth;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: $gutterWidth;

    img {
      width: 45px; }

    .tool-name {
      opacity: 0.5; } } }
